<template>
  <b-card v-if="request.respondId.length !== 0" no-body class="mb-3 request">
    <b-card-header
      header-tag="header"
      class="p-1 d-flex pr-3 pl-3 header"
      role="tab"
      @click="collapse(request.id + 1)"
    >
      <div class="request-header">
        <h2>{{ request.name }}</h2>
        <span class="mr-3">Город: {{ request.city }}</span>
        <span>Бютжет: {{ request.budjet + "р" }}</span>
      </div>
      <button class="ml-auto">
        <img :class="shevroneClass" src="./img/shevrone_down.svg" />
      </button>
    </b-card-header>
    <b-collapse class="responds" :id="request.id + 1">
      <b-card-body>
        <Respond v-for="id in request.respondId" :key="id" :id="id" />
      </b-card-body>
    </b-collapse>
  </b-card>
</template>

<script>
import { mapGetters } from "vuex";
import Respond from "./respond/Respond";

export default {
  name: "RespondsGroup",
  data: () => ({
    isOpen: false
  }),
  methods: {
    collapse(id) {
      this.isOpen = !this.isOpen;
      this.$root.$emit("bv::toggle::collapse", id);
    }
  },
  props: ["request"],
  computed: {
    ...mapGetters(["allRequests"]),
    shevroneClass() {
      if (this.isOpen) return "shevron-down";
      return "";
    }
  },
  components: {
    Respond
  }
};
</script>

<style lang="scss" scoped>
.request {
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.35);

  &:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.55);
  }

  .request-header {
  }

  button {
    background: transparent;
    border: none;
    width: 47px;

    img {
      height: 30px;
    }

    &:focus {
      outline: none;
    }
  }
}

.shevron-down {
  transform: rotate(-90deg);
}

.header {
  cursor: pointer;
  background-color: $white;
}
</style>
