<template>
  <b-card class="respond" @click="goToRespond">
    <div class="header mb-2">
      <div class="avatar">
        <span v-if="contractor.avatar === null">{{
          contractor.name.slice(0, 1)
        }}</span>
      </div>

      <div @click.stop="goToContractor" class="contractor-link">
        <h2 class="mb-0 ml-2">{{ contractor.name }}</h2>
      </div>

      <button @click.stop="refuseRespond" class="btn btn-secondary ml-auto">
        Отклонить отклик
      </button>
    </div>

    <div class="separator mb-2"></div>

    <div class="send-change d-flex mb-2">
      <span class="time mr-2">Отправлено: {{ respond.creationTime }}</span>
      <span class="time" v-if="respond.creationTime !== respond.updateTime">
        Изменено: {{ respond.updateTime }}
      </span>
    </div>

    <div class="body">
      <div class="info">
        <div class="mb-1">
          <span class="mr-3">Оценка сроков: {{ respond.period }}</span>

          <span>Оценка стоимости: {{ respond.cost + "р" }}</span>
        </div>

        <div>{{ respond.description }}</div>
      </div>

      <div class="status ml-auto">
        <div>Статус отклика:</div>
        <div>{{ respond.status }}</div>
      </div>
    </div>
  </b-card>
</template>

<script>
export default {
  name: "Respond",
  props: ["id"],
  computed: {
    respond() {
      return this.$store.getters.getRespondById(this.id);
    },
    contractor() {
      return this.$store.getters.getContractor;
    }
  },
  methods: {
    goToRespond() {
      this.$router.push("/responds/" + this.respond.id);
    },
    refuseRespond() {
      this.$router.push("/refuse/" + this.respond.id); //убрать
    },
    goToContractor() {
      this.$router.push("/contractor/" + this.contractor.id);
    }
  }
};
</script>

